'use client';

import React from 'react';
import Link from 'next/link';
import DepartamentosPage from '../components/departamentos/DepartamentosPage';
import MunicipiosPage from '../components/municipios/MunicipiosPage';
import ColegiosPage from '../components/colegios/ColegiosPage';
import SedesPage from '../components/sedes/SedesPage';
import UsuariosPage from '../components/usuarios/UsuariosPage';
import { usePathname } from 'next/navigation';

export default function Home() {
  const pathname = usePathname();

  const getComponent = () => {
    switch (pathname) {
      case '/departamentos':
        return <DepartamentosPage />;
      case '/municipios':
        return <MunicipiosPage />;
      case '/colegios':
        return <ColegiosPage />;
      case '/sedes':
        return <SedesPage />;
      case '/usuarios':
        return <UsuariosPage />;
      default:
        return <DepartamentosPage />;
    }
  };

  const navLinks = [
    { href: '/departamentos', label: 'Departamentos' },
    { href: '/municipios', label: 'Municipios' },
    { href: '/colegios', label: 'Colegios' },
    { href: '/sedes', label: 'Sedes' },
    { href: '/usuarios', label: 'Usuarios' },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      <nav className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16">
            <div className="flex">
              <div className="flex-shrink-0 flex items-center">
                <Link 
                  href="/"
                  className="flex items-center space-x-2"
                >
                  <svg 
                    className="h-8 w-8 text-blue-600" 
                    viewBox="0 0 24 24" 
                    fill="none" 
                    stroke="currentColor" 
                    strokeWidth="2"
                  >
                    <path 
                      strokeLinecap="round" 
                      strokeLinejoin="round" 
                      d="M12 14l9-5-9-5-9 5 9 5z"
                    />
                    <path 
                      strokeLinecap="round" 
                      strokeLinejoin="round" 
                      d="M12 14l9-5-9-5-9 5 9 5z" 
                      opacity=".2"
                    />
                    <path 
                      strokeLinecap="round" 
                      strokeLinejoin="round" 
                      d="M12 21l9-5-9-5-9 5 9 5z" 
                      opacity=".2"
                    />
                  </svg>
                  <span className="text-xl font-bold text-gray-900">Colegio App</span>
                </Link>
              </div>
              <div className="hidden sm:ml-6 sm:flex sm:space-x-8">
                {navLinks.map((link) => (
                  <Link
                    key={link.href}
                    href={link.href}
                    className={`inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium transition-colors duration-200 ${
                      pathname === link.href
                        ? 'border-blue-500 text-gray-900'
                        : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                    }`}
                  >
                    {link.label}
                  </Link>
                ))}
              </div>
            </div>
          </div>
        </div>
      </nav>

      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-4 sm:px-0">
          {getComponent()}
        </div>
      </main>

      <footer className="bg-white border-t border-gray-200 mt-auto">
        <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
          <p className="text-center text-sm text-gray-500">
            {new Date().getFullYear()} Colegio App. Todos los derechos reservados.
          </p>
        </div>
      </footer>
    </div>
  );
}